function getRandomInt(min: number, max: number) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min)) + min; //Максимум не включается, минимум включается
}

function testFunc(){
  let elem = document.getElementById("result");
  let r = getRandomInt(1, 5);
  if(r==1) {
    elem!.innerHTML = "Результат: Душный";
  }
  if(r==2) {
    elem!.innerHTML = "Результат: Веселый";
  }
  if(r==3) {
    elem!.innerHTML = "Результат: Токсичный";
  }
  if(r==4) {
    elem!.innerHTML = "Результат: Гоша";
  }
  if(r==5) {
    elem!.innerHTML = "Результат: Грустный";
  }
  
    
}


